.profile {
  display: flex;
  align-items: center;
  margin-top: 1.5rem;
  width: 100%;

  &__img {
    width: 3rem;
    height: 3rem;
    filter: grayscale(100%);
    border-radius: 50%;
  }

  &__social {
    list-style: none;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
  }

  &__link {
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 0.8rem;
    transition: 0.3s;

    &:hover {
      color: aquamarine;
      transform: translateY(-2px);
    }
  }

  &__icon {
    font-size: 1.2rem;
    margin-left: 0.2rem;
  }
}

@media screen and (min-width: 768px) {
  .profile {
    &__img {
      height: 4rem;
      width: 4rem;
    }

    &__link {
      font-size: 1rem;
    }
  }
}
